* {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

body {
    font: 14px/1.42 'Lato', sans-serif;
    text-align: center;
    color: #444;
    background: url(../../images/restaurant_icons.png) 0 0 repeat;
    overflow-x: hidden;
    word-wrap: break-word;
    text-rendering: optimizeLegibility;
}

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, address, cite, code, del, dfn, em, img, ins, q, small, strong, sub, sup, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, input, table, caption, tbody, tfoot, thead, tr, th, td, figure {
    border: 0;
    margin: 0;
    padding: 0
}

h1, h2 {
    font-family: 'Lobster Two', cursive;
}

html {
    overflow-y: scroll;
    -webkit-text-size-adjust: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-tap-highlight-color: #80CBB8;
}

img, fieldset, abbr, acronym {
    border: 0;
}

article, aside, figure, figcaption, hgroup, footer, header, nav, section, video, object {
    display: block;
}

audio, canvas, video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    max-width: 100%;
}

/*LAYOUT*/
.head {
    float: left;
    width: 100%;
    position: relative;
    z-index: 2;
    height: 105px;
}

.head .hello {
    height: 105px;
    line-height: 105px;
    position: relative;
    float: right;
    padding: 0 10px;
}

.home .head {
}

.head .wrap {
    height: 100%;
    background: #fff;
    -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, .2);
    -moz-box-shadow: 0 1px 4px rgba(0, 0, 0, .2);
    box-shadow: 0 1px 4px rgba(0, 0, 0, .2);
    display: inline-block;
}

.main {
    float: left;
    width: 100%;
    display: block;
    min-height: 400px;
    padding: 0 0 0 0;
    margin: 50px 0 0 0;
}

.foot {
    float: left;
    width: 100%;
}

.wrap {
    margin: 0 auto;
    width: 1170px;
    text-align: left;
    position: relative;
    max-width: 96%;
}

.row {
    margin: 0 -15px;
}

.full-width {
    float: left;
    width: 100%;
    padding: 0 15px 30px;
    max-width: 100%;
}

.three-fourth {
    float: left;
    width: 75%;
    padding: 0 15px 30px;
    max-width: 100%;
}

.one-half {
    float: left;
    width: 50%;
    padding: 0 15px 30px;
    max-width: 100%;
}

.one-third {
    float: left;
    width: 33.33%;
    padding: 0 15px 30px;
    max-width: 100%;
}

.two-third {
    float: left;
    width: 66.66%;
    padding: 0 15px 30px;
    max-width: 100%;
}

.one-fourth {
    float: left;
    width: 25%;
    padding: 0 15px 30px;
    max-width: 100%;
}

.one-fifth {
    float: left;
    width: 20%;
    padding: 0 15px 30px;
    max-width: 100%;
}

.one-sixth {
    float: left;
    width: 16.66%;
    padding: 0 15px 30px;
    max-width: 100%;
}

.content {
    float: left;
    width: 100%;
}

.cwrap {
    float: left;
    width: 100%;
    overflow-x: hidden;
    border-bottom: 1px solid rgba(0, 0, 0, .15);
    -webkit-box-shadow: 0 1px 0 rgba(255, 255, 255, 1);
    -moz-box-shadow: 0 1px 0 rgba(255, 255, 255, 1);
    box-shadow: 0 1px 0 rgba(255, 255, 255, 1);
    margin: 0 0 30px;
}

.cwrap:last-child {
    border: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    margin: 0;
}

.entries .one-fourth {
    min-height: 365px;
}

.clearfix:before,
.clearfix:after,
.row:before,
.row:after {
    content: "";
    display: table;
}

.clearfix:after,
.row:after {
    clear: both;
}

.oldie .clearfix,
.oldie .row {
    zoom: 1;
}

::-moz-selection {
    background: #fff;
    color: #6EC4AE;
    -webkit-text-shadow: none;
    -moz-text-shadow: none;
    text-shadow: none;
}

::selection {
    background: #fff;
    color: #6EC4AE;
    -webkit-text-shadow: none;
    -moz-text-shadow: none;
    text-shadow: none;
}

