/*HEADER*/
.logo {
    float: left;
    display: block;
    font-size: 30px;
    padding: 5px 15px 0 15px;
    height: 100%;
    border-right: 15px solid #A292D3;
}

.logo img {
    margin:auto;
}

.main-nav {
    float: left;
}

.main-nav ul {
    position: relative;
    display: inline-table;
    z-index: 9999;
    border-left: 15px solid #93d3c3;
    padding: 0 0 0 15px;
}

.main-nav li {
    float: left;
    font-size: 13px;
    font-weight: 500;
    text-transform: uppercase;
}

.main-nav li a {
    color: #444;
    float: left;
    height: 105px;
    line-height: 105px;
    -webkit-transition: color 0.2s ease-in;
    -moz-transition: color 0.2s ease-in;
    -o-transition: color 0.2s ease-in;
    -ms-transition: color 0.2s ease-in;
    transition: color 0.2s ease-in;
}

.main-nav li a span {
    float: left;
    line-height: normal;
    margin: 35px 0 29px;
    padding: 8px 15px;
    -webkit-transition: all 0.2s ease-in;
    -moz-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
    -ms-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
}

.main-nav li:hover a,
.main-nav .current-menu-item a {
    color: #fff;
}

.main-nav li:hover span,
.main-nav .current-menu-item span {
    background: #a292d3;
    color: #fff;
}

.main-nav ul ul {
    display: none;
    width: 150px;
    margin-left: -30px;
    position: absolute;
    top: 100%;
    padding: 0 15px 10px;
    background: #fff;
    border-top: 3px solid #93d3c2;
    -webkit-border-radius: 0 0 3px 3px;
    -moz-border-radius: 0 0 3px 3px;
    border-radius: 0 0 3px 3px;
    -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, .15), 0 0 3px rgba(0, 0, 0, .15);
    -moz-box-shadow: 0 1px 0 rgba(0, 0, 0, .15), 0 0 3px rgba(0, 0, 0, .15);
    box-shadow: 0 1px 0 rgba(0, 0, 0, .15), 0 0 3px rgba(0, 0, 0, .15);
}

.main-nav ul ul:after {
    content: "";
    position: absolute;
    top: -11px;
    left: 43%;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 8px 8px 8px;
    border-color: transparent transparent #93d3c2 transparent;
}

.main-nav ul ul li {
    float: none;
    position: relative;
    background: none;
    text-transform: none;
}

.main-nav ul ul li a {
    height: auto;
    line-height: normal;
    padding: 10px 0;
    border-bottom: 1px solid #ddd;
    color: #666;
    width: 100%;
}

.main-nav ul ul li span {
    padding: 0;
    background: none !important;
    margin: 0;
    color: #666 !important;
}

.main-nav ul ul ul {
    position: absolute;
    left: 100%;
    top: 0;
    margin-left: 0;
}

.main-nav ul ul li:last-child a {
    border-bottom: none;
    padding-bottom: 0;
}

.main-nav li:hover > ul {
    display: block;
}

.main-nav li:hover ul a,
.main-nav .current-menu-item ul a {
    color: #444;
}

.main-nav ul ul li:hover a,
.main-nav ul ul li:hover span,
.main-nav ul ul .current-menu-item a {
    color: #6EC4AE !important;
}

.user-nav {
    /* float:right;width:270px; */
    position: relative;
    top: 0;
    right: 0;
}

.user-nav li {
    float: right;
    width: 90px;
    height: 105px;
    font-size: 11px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    overflow: hidden;
}

.user-nav li a {
    color: #fff;
    float: left;
    width: 100%;
    height: 100%;
}

.user-nav .light a {
    background: #93D3C3;
}

.user-nav .medium a {
    background: #80CBB8;
}

.user-nav .dark a {
    background: #6EC4AE;
}

.user-nav .ico {
    margin-top: 14px;
}

.user-nav li span {
    float: left;
    width: 100%;
    padding: 10px 10px 0;
}

.user-nav li.noicon span {
    padding:0;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.user-nav li.avatar {
    width: 105px;
}

.user-nav li:hover a,
.user-nav .current-menu-item a {
    background: #FFF;
}

.user-nav .light.current-menu-item a,
.user-nav .light:hover a {
    color: #93D3C3;
}

.user-nav .medium.current-menu-item a,
.user-nav .medium:hover a {
    color: #80CBB8;
}

.user-nav .dark.current-menu-item a,
.user-nav .dark:hover a {
    color: #6EC4AE;
}

.user-nav .light:hover .i-search,
.user-nav .current-menu-item .i-search {
    background-position: -27px 0;
}

.user-nav .medium:hover .i-account,
.user-nav .current-menu-item .i-account {
    background-position: -80px 0;
}

.user-nav .dark:hover .i-submitrecipe,
.user-nav .current-menu-item .i-submitrecipe {
    background-position: -133px 0;
}

.head select {
