    .logo {
        margin-left: 0;
        margin-right: 0;
        width: 100%;
        text-align: center;
    }

    .logo img {
        margin: 0 auto;
    }

    .user-nav {
        top: 90px;
    }

    .slicknav_btn {
        top: 90px;
        left: 0;
        right: auto;
    }

    .slicknav_nav {
        top: 180px;
    }

    .recipe .one-fourth dl {
        width: 100%;
        padding: 0;
    }

    .main .widget {
        width: 100%;
        margin-right: 0;
    }

    .post {
        padding-left: 0;
    }

    .post .entry-meta {
        position: static;
        width: 100%;
    }

    .post .actions > div {
        border: none;
        padding-top: 0;
        margin: 0;
    }

    .actions div div {
        padding: 10px 0 !important;
        margin: 0;
        width: 100% !important;
        border-right: 0;
        border-bottom: 1px solid #ddd;
    }

    .comments .depth-2,
    .comments .depth-3 {
        padding-left: 90px;
    }

    .depth-2 .avatar,
    .depth-3 .avatar {
        left: 0;
    }

    .comment-reply-link {
        position: static;
        margin: 0 10px;
        color: #444 !important;
        background: none;
        padding: 0;
        float: none;
        font-size: 13px;
    }

    .one-third figure,
    .one-third .container,
    .two-third figure,
    .two-third .container,
    .full-width .entries .entry figure,
    .full-width .entries .entry .container {
        width: 100% !important;
    }

    section.cta .wrap {
        text-align: center;
    }

    section.cta h2 {
        width: 100%;
        float: left;
    }

    section.cta .button {
        float: none;
        margin: 0 0 20px;
    }

    .ingredient .third,
    .submit_recipe .instruction .full {
        width: 80%;
    }

    .above-sidebar .boxed li {
        width: 33.33%;
    }

    .pricing div {
        width: 100%;
        margin: 0 0 30px !important;
    }

    .textwrap,
    .intro .widget form,
    .icons .one-sixth {
        width: 100%;
    }

    .intro .widget form {
        padding: 20px 0 0;
    }

    .home .s-title {
        text-align: center;
    }

    h2.large {
        background-size: 100% 123px;
        font-size: 24px;
    }

    .featured:first-child h2, h2.bright {
        margin: 0 auto 20px;
    }

    .tab-content .basic dt {
        width: 100%;
        margin: 0;
    }

    .tab-content .basic dd {
        width: 100%;
    }

    .recipefinder input[type="submit"] {
        width: 100%;
        margin-bottom: 15px;
    }

    .checkbox div.checker, .radio div.radio {
        float: left;
    }

    .checkbox label, .radio label {
        float: left;
        width: 80%;
    }
}

@media (max-width: 360px) {
    .logo {
        text-align: left;
        width: auto;
        max-width: 80%;
        margin-left: 15px;
    }

    .user-nav {
        width: 100%;
    }

    .user-nav li {
        width: 33.33%;
    }

    .slicknav_btn {
        top: 0;
        right: 0;
        left: auto;
    }

    .slicknav_nav {
        top: 90px;
    }

    .quicklinks .button {
        width: 90%;
        margin: 0 0 15px;
    }
}
